import { Stack } from '@mui/material';
import classnames from 'classnames';
import { ThreeDRotation, Mic, Videocam, People, QuestionAnswer, Close } from '@mui/icons-material';

import { CasterFooterIconButton, CasterFooterSwitchButton } from './CasterFooterButton';

import styles from './CasterFooter.module.scss';

interface TProps {
  className?: string;
  useSwitchButtons?: boolean;
}

export function CasterFooter(props: TProps) {
  const { className, useSwitchButtons } = props;
  const ButtonComponent = useSwitchButtons ? CasterFooterSwitchButton : CasterFooterIconButton;
  // prettier-ignore
  return (
    <Stack className={classnames(className, styles.root)} flexDirection="row" gap={2}>
      <Stack
        className={classnames(styles.buttons, styles.leftGroup)}
        flex={1}
        flexDirection="row"
        justifyContent="flex-start"
      >
        <ButtonComponent text="VR" icon={ThreeDRotation} checked checkable />
        <ButtonComponent text="Audio" icon={Mic} checkable />
        <ButtonComponent text="Video" icon={Videocam} checked checkable />
        <ButtonComponent text="Participants" icon={People} badgeNumber={5} />
        <ButtonComponent text="Chat" icon={QuestionAnswer} clickable />
      </Stack>
      <Stack
        className={classnames(styles.buttons, styles.rightGroup)}
        flexDirection="row"
        justifyContent="flex-end"
      >
        <ButtonComponent text="End" icon={Close} theme="alert" clickable />
      </Stack>
    </Stack>
  );
}
